import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { Image, SearchContainer } from '../styles';
import 'boxicons'
const styles = {
    formContainer: {
        margin: "0 auto",
        position: "relative",
        width: "40em",
        top: "100%"
    },
    formm: {
        position: "relative",
        top: "-1.3em"
    },
    formInput: {
        width: "100%",
        height: "3em",
        paddingInline: "1em",
        fontSize: "1em",
        letterSpacing: "2.2px",
    },
    SearchIcon: {
        border: "none",
        width: "4.8em",
        padding: "5px 0px 5px 0px",
        float: "right",
        position: "relative",
        top: "-3em",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        backgroundColor: "transparent"
    },
}
function HomepageSearchContainer () {
    const navigate = useNavigate()
    const [movieInput, setMovieInput] = useState('')
    

    const findMovie = async (title) => {
        const params = {
            param1: title
        }
        fetch(`http://www.omdbapi.com/?apikey=${process.env.REACT_APP_OMDB_API_KEY}&s=${title}&plot=full`)
        .then(res => res.json())
        .then(data => navigate(`/results/${title}`, { state: data}))
    }
    const handleSubmit = (e) => {
        e.preventDefault()
        findMovie(movieInput)
    }

    return (
        <SearchContainer>
            <div style={styles.formContainer}>
                <form style={styles.formm}>
                    <input 
                        style={styles.formInput} 
                        placeholder="Search..."
                        value={movieInput}
                        onChange={(e) => setMovieInput(e.target.value)}
                    />

                    <button style={styles.SearchIcon} onClick={handleSubmit}><img src="data:image/png;base64,"/></button>
                </form>
            </div>
        </SearchContainer>
    )
}

export default HomepageSearchContainer;